.mainnotes {
  margin: 0;
  padding: 0 2rem;
  height: 85%;
}

.total-notes {
  overflow-y: scroll;

}

.dark .note {
  background-image: url("../../../public/svg/darknote.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  padding: 0;
  margin-left: 2rem;
  font-family: "sans-serif";
  font-weight: 900;
  margin-top: 2rem;
  font-size: 3rem;
  font-style: normal;
  min-width: 250px;
  min-height: 201px;
  // box-shadow: 0px 2px 0px 0px gray;

  padding: 0 1%;
}

.light .note {
  background-image: url("../../../public/svg/notes.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  padding: 0;
  margin-left: 2rem;
  font-family: "sans-serif";
  font-weight: 900;
  margin-top: 2rem;
  font-size: 3rem;
  font-style: normal;
  min-width: 250px;
  min-height: 201px;


  // box-shadow: 0px 2px 0px 0px gray;

  padding: 0 1%;
}

.lightnote {
  color: black;
}

.darknote {
  color: #ffffff;
}

.light .notetext {
  margin-left: 6%;
  margin-top: 0%;
  width: 88%;
  padding-top: 15%;
  text-align: justify;
  word-wrap: wrap;
  padding-bottom: 10%;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
  /* text-overflow: clip; */
  overflow-wrap: break-word;
  overflow: hidden;
  color: black;

}

.dark .notetext {
  margin-left: 6%;
  margin-top: 0%;
  width: 88%;
  padding-top: 15%;
  text-align: justify;
  word-wrap: wrap;
  padding-bottom: 10%;
  color: white;

  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
  /* text-overflow: clip; */
  overflow-wrap: break-word;
  overflow: hidden;
}

.darknotetext {
  border-top: 2px solid rgb(255, 255, 255, 0.2);
}

.lightnotetext {
  border-top: 2px solid rgb(0, 0, 0, 0.1);
}

