.footerpara {
  margin-left: 6%;
  width: fit-content;
  text-align: justify;
  margin-bottom: 0;
  margin-top: 0;
  color: #7496b8;
}

.darknotesfooter {
  position: relative;
  position: relative;
  /* bottom: 0%; */
  padding-top: 1%;
  top: 23%;
}

.deletenote {
  position: relative;
  left: 82%;
  height: 60%;
  width: 9%;
  transform: translateY(-97%);
}

.light .footer {
  color: #7496b8;
  font-family: sans-serif;
  margin-bottom: 0.5rem;
  font-weight: 200;
  height: fit-content;
  position: fixed;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  width: 100%;
  bottom: 1rem;
  border-top: 1px solid rgba(0, 0, 0, 0.14);
}

.dark .footer {
  color: #7496b8;
  font-family: sans-serif;
  margin-bottom: 0.5rem;
  font-weight: 200;
  height: fit-content;
  position: fixed;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  width: 100%;
  bottom: 1rem;
  border-top: 1px solid rgb(255, 255, 255, 0.14);
}

